import React, { Component }   from 'react';
import { LocationMapIframe }  from './';
import './LocationMap.styl';

/* eslint-disable */




class LocationMap extends Component {

    render() {
        const { locations, toggleCityMap, cityMapToShow } = this.props;
        const cityToShow = locations.filter((location, i) => (
            location.name === cityMapToShow
        ))[0];
        // const ListOptions = locations.map((location, i) => ({ value: location.name, label: location.name }));

        return (
                <div className="location-map-wrapper">
                    <div className="location-map">
                        <div className = "location-map__select-wrapper">
                            <select className = "location-map__select"
                                    value     = { cityMapToShow }
                                    onChange  = { toggleCityMap }>
                                {
                                    locations && locations.map((location, i) => (
                                        <option key   = { i }
                                                value = { location.name }>
                                            { location.name }
                                        </option>
                                    ))
                                }
                            </select>
                        </div>
                            {
                                cityToShow && (
                                    <LocationMapIframe iframeSrc={ cityToShow.iframeSrc } />
                                )
                            }
                    </div>
                </div>
        )
    };
};


export default LocationMap;





