import React, { Component }    from 'react';
import { Button }      from './../../../_UI';
import './CoursesType.styl';




/* eslint-disable */

class CoursesType extends Component {
    render() {
        const { data }               = this.props,
              { title, price, info } = data;
        return (
                <div className="courses__type">
                    <div className="courses__type-header">
                        <h3 className="courses__type-header-title">
                            {
                                !!title && title.map((el, i) => <span key={ i }>{ el }</span>)
                            }
                        </h3>
                        {
                            price && (<p className="courses__type-header-price">{ price } грн в месяц</p>)
                        }
                    </div>
                    <ul className="courses__type-info">
                        {
                            !!info && info.map((el, i) => <li className="courses__type-info-item" key={ i }>{ el }</li>)
                        }
                    </ul>
                    <p className="courses__type-curriculum">подробная программа</p>
                    <Button text="Отправить заявку" className="courses__type-btn"/>
                </div>
        )
    }
};



export default CoursesType;



