import React, { Component }              from 'react'
import { connect }                       from 'react-redux';
import { MainInfoActions }               from './../../actions';
import { Header, Footer }                from './../';
import { Loader, ErrorModal }            from './../_UI';
import './WithHeaderAndFooter.styl';

const mapStoreToProps = store => ({
    mainInfoReducer: store.mainInfoReducer
});

@connect(mapStoreToProps)
class WithHeaderAndFooter extends Component {
    componentWillMount() {
        this.props.dispatch(MainInfoActions.getMainInfo());
    };
    render() {
        const { children, mainInfoReducer }  = this.props,
              { error, isError, isLoading }  = mainInfoReducer;
        return (
            <div className="app-wrapper">
                {
                    !isError
                    ? (!isLoading
                        ? (<div className="app">
                            <Header mainInfoReducer={ mainInfoReducer }/>
                            <div className="page-wrapper">
                                { children }
                            </div>
                            <Footer mainInfoReducer={ mainInfoReducer }/>
                            </div>)
                            : ( <Loader /> ))
                        : (<ErrorModal error={ error }/>)
                }
            </div>
        )
    }
};

export default WithHeaderAndFooter;


