import React, { Component }   from 'react';
import './Footer.styl';

/* eslint-disable */

class Footer extends Component {
    render() {
        return (
                <div className="footer-wrapper">
                    <div className="footer">
                        <div className="footer__contacts">
                            <h4 className="footer__contacts-title">Контакты</h4>
                            <p className="footer__contacts-phone">+38 (050) 706-81-43</p>
                            <a className="footer__contacts-email" href="mailto:info@php-academy.kiev.ua">info@php-academy.kiev.ua</a>
                            <div className="footer__contacts-office">
                                <p>Киев</p>
                                <p>ул. Михаила Коцюбинского, 14, ст. метро Университет</p>
                            </div>
                        </div>
                        <div className="footer__about">
                            <p>PHP Academy - школа веб-программирования №1 в Украине. Мы предоставляем курсы с документальной гарантией стажировки, прописанной в договоре.</p>
                            <p>Узнать больше</p>
                        </div>
                        <div className="footer__copyright">
                            <p className="footer__copyright-owner"> © ФЛП Шумар О.И. <span> | </span></p>
                            <p className="footer__copyright-date">2014-{ new Date().getFullYear() }</p>
                        </div>

                    </div>
                </div>
        )
    };
};


export default Footer;





