import React, { Component }   from 'react';
import { withRouter }         from 'react-router';
import { pathes }             from './../../constants';
import {
    TabConstructor,
    LogoTab,
    AboutTab,
    StudentsTab,
    VideoTutorialsTab
}                             from './';


import './Header.styl';

/* eslint-disable */


@withRouter
class Header extends Component {
    render() {
        const { data }     = this.props.mainInfoReducer,
              { pathname } = this.props.router.location;


        const isMainPage = !( pathname.length - 1);
        return (
                <header className="header">
                    <div className="header__navbar">
                        <LogoTab isMainPage={ isMainPage }/>
                        <TabConstructor className = "about"
                                        path = { `/${ pathes.about }` }
                                        text = "О нас" />
                        <TabConstructor className = "students"
                                        path = { `/${ pathes.students }` }
                                        text = "Студентам" />
                        <TabConstructor className = "curriculums"
                                        path = { `/${ pathes.curriculum }` }
                                        text = "Программа курсов" />
                        <TabConstructor className = "partners"
                                        path = { `/${ pathes.partners }` }
                                        text = "Партнерам" />
                        <TabConstructor className = "contacts"
                                        path = { `/${ pathes.contacts }` }
                                        text = "Контакты" />
                        <TabConstructor className = "fullstack"
                                        path = { `/${ pathes.fullstack }` }
                                        text = "FullStack 2017" />
                    </div>
                </header>
        )
    };
};



export default Header;





