                [(<p className="category-text"> На выбор представлена классическая никотиновая база и премиум класса. Отличаются они качеством используемого никотина. В остальном, в составе присутствуют пищевые пропиленгликоль и глицерин, а также дистиллированная вода. В классическом варианте базы добавляется никотин <b>«Шанхайская сотка»</b>, в премиум-классе – <b>Органический никотин</b>, произведённый по стандартам рынка США. Объём и пропорциональное содержание пропиленгликоля и глицерина выбираются по усмотрению, что дополнительно индивидуализирует итоговый получаемый вкус и концентрацию готовой никотиновой базы для электронной сигареты. Отметим также, что несмотря на всеобщее негативное отношение к никотину, в столь малых дозах он не несёт вреда организму. Напомним, что основной вред и опасность традиционных табачных сигарет состоит в более, чем сотне использующихся канцерогенных веществ, мышьяке, бутаноле и ряду продуктов горения, которые испаряются и вдыхаются при курении классических сигарет. Для вейпинга используются только пищевые пропиленгликоль и глицерин, пищевые ароматизаторы и опционально никотин. За счёт этого вейпинг считается безопасным способом отказа от курения.</p>)],
