                [(<p className="category-text"> Опытные вейперы или начинающие, почувствуйте себя современным алхимиком – <b>сделайте самостоятельно жидкость для электронной сигареты</b>. Для этого воспользуйтесь комплектом для приготовления жидкости, в которых входят пищевой пропиленгликоль, пищевой глицерин, супер основа или никотиновая основа «Шанхайская сотка». Берите смелее разные флакончики и капельницы и начинайте приготовление собственноручно вымеренной и дозированной в каждом компоненте жидкости. Это можно сделать индивидуальным подбором пропорций или следовать рецептам, найденным на специализированных форумах. Увлечённое вейпингом сообщество раскрывает секреты всевозможных готовых жидкостей, в том числе и премиальных, кроме того, вы найдёте проверенные рецепты миксов с использованием аромок разных производителей, что довольно нетипично, исходя из рекомендаций самих производителей. Тем не менее, комплект для приготовления жидкости основы для вейпа позволяет вам индивидуально решать её дальнейшее насыщение тем или иным вкусом.</p>)],
                [(<p className="category-text"> Полагаем, удовольствие от создания чего-то самостоятельно увлекательно. Да и в большей мере предполагает удовлетворённость конечным результатом, ведь то, что вы получили, вы сделали собственными руками. После приготовления жидкости для электронной сигареты остаётся только добавить вкуса, но тут уже выбор не ограничен – даже самые смелые ожидания или личные предпочтения, за счёт количества производителей и представляемыми ими вкусами, удовлетворятся. Только на нашем сайте вы найдёте более 500 вкусов ароматизаторов для электронной сигареты. Уверяем, среди них встретятся и такие гастрономические вкусы, которые вы не пробовали в реальной жизни. Помимо ароматизаторов доступны специфические добавки, которые не отразятся на вкусе базовой жидкости для вейпа, но добавят особые ощущения. Так cooling agent добавит прохлады, может использоваться отдельно или в сочетании с аромками. Ацетил Пиразин усилит вкус жидкости, особенно гармонично сочетается с ореховыми, кремовыми ароматизаторами. Однако данную добавку не рекомендуется использовать как отдельную именно для вкуса.</p>)],
                [(<p className="category-text"> На выбор в Nanosmoking предлагается купить <b>«Комплект 220»</b> и <b>«Комплект 500»</b>, в которых отличаются представленная никотиновая основа и объём глицерина и пропиленгликоля в комплекте. Начинающим вполне подойдёт комплект поменьше для экспериментов, опытные – выбирайте набор пообъёмнее, ведь при правильно вымеренной индивидуальной дозировке такого комплекта хватит надолго, а это выгодно. Воспользуйтесь для смеси флаконами с капельницами или дозаторами, таким образом вы максимально точно, до миллиграмма подберёте пропорцию базовых компонентов для жидкости электронной сигареты. Готовую базу можно разлить по флаконам меньшего объёма и добавить разные ароматизаторы. Таким образом, вы сделаете заготовку из скольки угодно вкусов, которые в любой момент будут под рукой. Вам останется только придавать ароматно-вкусового окраса парению. Получайте удовольствие от вейпинга. А если возникнут вопросы, обращайтесь к консультантам Nanosmoking, и вам подскажут интересующую информацию о каждом аспекте парения.</p>)]
