import React, { Component }    from 'react';
import {
    StudentsDiscountDescription,
    StudentsDiscountArrows,
    StudentsDiscountSvg
}                              from './';
import './StudentsDiscount.styl';



/* eslint-disable */

class StudentsDiscount extends Component {
    render() {
        return (
            <section className="students-discount-wrapper">
                <div className="students-discount">
                    <StudentsDiscountSvg />
                    <StudentsDiscountArrows />
                    <StudentsDiscountDescription />
                </div>
            </section>

        )
    }
};






export default StudentsDiscount;



