import React, { Component }    from 'react';
import { CoursesType } from './';
import './Courses.styl';




/* eslint-disable */

class Courses extends Component {
    render() {
        const { coursesTypes } = this.props,
              { title, data }  = coursesTypes;
        return (
                <section className="courses-wrapper">
                    <div className="courses">
                        <h4 className="courses__title">{ title }</h4>
                        {
                            data && data.map((type, i) => <CoursesType data={ type } key={ i } />)
                        }
                    </div>
                </section>
        )
    }
};



export default Courses;



