import React, { Component }    from 'react';
import './NewCoursesOffices.styl';




/* eslint-disable */

class NewCoursesOffices extends Component {
    render() {
        const { info }                        = this.props,
              { locations, amountToDiscount } = info;
        return (
                <div className="new-courses__offices">
                    <h4 className="new-courses__offices-title">А у нас новые офисы</h4>
                    <span className="new-courses__offices-stars">{ "\u2605" }{ "\u2605" }{ "\u2605" }{ "\u2605" }{ "\u2605" }</span>
                    <table className="new-courses__offices-info">
                        <thead>
                            <tr>
                                <th>Офис</th>
                                <th>Места</th>
                                <th>Скидка</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                locations && locations.map((office, i) => (
                                    <tr key={ i }>
                                        <td>{ office.location }</td>
                                        <td>{ office.balance }/30</td>
                                        <td>получить скидку 50%</td>
                                    </tr>
                                ))
                            }
                        </tbody>
                    </table>
                    <div className="new-courses__offices-discount">
                        для первых <b>{ amountToDiscount }</b> студентов в каждом новом филиале скидка <b>50%</b>.
                    </div>
                </div>
        )
    }
};




