import React, { Component }   from 'react';
import './LocationsSvgWink.styl';

/* eslint-disable */




class LocationsSvgWink extends Component {
    render() {
        const {  handleClick, city, street, house, cx, cy } = this.props;
        const tipW = "199.844983",
              tipH = "92.248072";

        return (
            <g className="locations__svg-wink-wrapper">
                <ellipse opacity     = "0"
                         strokeWidth = "14"
                         stroke      = "#02b3e4"
                         ry          = "5.357143"
                         rx          = "4.910714"
                         cx          = { cx }
                         cy          = { cy }
                         data        = { city }
                         onClick     = { handleClick }
                         fill        = "#02b3e4"/>


                <g className = "locations__svg-wink-info"
                   onClick   = { handleClick }>


                    <rect className   = "temp"
                          data        = { city }
                          stroke      = "#000000"
                          height      = { 20 }
                          width       = { (2/3) * parseFloat(tipW) }
                          y           = { parseFloat(cy) - 20 }
                          x           = { parseFloat(cx) - tipW/3 }
                          fillOpacity = "null"
                          strokeWidth = "0"
                          fill        = "transparent"/>


                    <line stroke      = "#409df9"
                          data        = { city }
                          y2          = { parseFloat(cy) - 20 }
                          x2          = { cx }
                          y1          = { cy }
                          x1          = { cx }
                          fillOpacity = "null"
                          fill        = "none"/>


                    <rect stroke      = "#000000"
                          data        = { city }
                          height      = { tipH }
                          width       = { tipW }
                          y           = { parseFloat(cy) - 20 - parseFloat(tipH) }
                          x           = { parseFloat(cx) - 92.248072 }
                          strokeWidth = "0"
                          fill        = "#409df9"/>

                    <text stroke      = "#ffffff"
                          data        = { city }
                          fontFamily  = ""
                          fontSize    = "18"
                          fontWeight  = "600"
                          y           = { parseFloat(cy) - 77 }
                          x           = { parseFloat(cx) - 72 }
                          strokeWidth = "0"
                          fill        = "#ffffff">
                        { city }
                    </text>
                    <text stroke      = "#ffffff"
                          data        = { city }
                          textAnchor  = "start"
                          fontFamily  = ""
                          fontSize    = "14"
                          y           = { parseFloat(cy) - 55 }
                          x           = { parseFloat(cx) - 72 }
                          strokeWidth = "0"
                          fill        = "#ffffff">
                        { street }
                    </text>
                    <text stroke      = "#ffffff"
                          data        = { city }
                          textAnchor  = "start"
                          fontFamily  = ""
                          fontSize    = "14"
                          y           = { parseFloat(cy) - 36 }
                          x           = { parseFloat(cx) - 72 }
                          strokeWidth = "0"
                          fill        = "#ffffff">
                        д. { house }
                    </text>
                </g>
            </g>
        )
    };
};


export default LocationsSvgWink;





