import React, { Component }    from 'react';
import './Statistic.styl';





/* eslint-disable */

class Statistic extends Component {
    render() {
        const { statisticInfo } = this.props;
        return (
                <section className="statistic-wrapper">
                    <div className="statistic">
                        {
                            statisticInfo && statisticInfo.map((block, i) => (
                                <div className="statistic__block" key={ i }>
                                    <div className="statistic__block-amount">{ block.amount }</div>
                                    <div className="statistic__block-description">{ block.description }</div>
                                </div>
                            ))
                        }
                    </div>
                </section>
        )
    }
};



export default Statistic;



