import React, { Component }   from 'react';
import {
    LocationMap,
    LocationsSvg
}                             from './';
import './Locations.styl';

/* eslint-disable */

class Locations extends Component {
    constructor() {
        super();
        this.state = {
            cityMapToShow: "Киев"
        }
    }
    toggleCityMap = (e) => {
        const cityMapToShow =  e.target.getAttribute("data") || e.target.value;
        console.log("toggle LocationMap", cityMapToShow);
        this.setState({
            cityMapToShow
        });
    };
    render() {
        const { locations }     = this.props,
              { cityMapToShow } = this.state,
              { toggleCityMap } = this;
        return (
                <section className    = "locations">
                    <h4 className = "locations__title">
                        Наши офисы
                    </h4>
                    <LocationMap locations     = { locations }
                                 toggleCityMap = { toggleCityMap }
                                 cityMapToShow = { cityMapToShow }/>


                    <LocationsSvg locations     = {  locations}
                                  toggleCityMap = { toggleCityMap }/>
                </section>
        )
    };
};


export default Locations;





