import React, { Component } from 'react';
import { Link }             from 'react-router';


import './SearchTab.styl';


/* eslint-disable */

class SearchTab extends Component {

    render() {

        return (
            <li className="header-nav__tab search-tab">
                <Link activeClassName="is-active" className="mi mi-search" />
            </li>
        )
    }
}



export default SearchTab;


