import React, { Component } from 'react';
import { Link }             from 'react-router';
import { pathes }           from './../../../constants';
import './StudentsTab.styl';

/* eslint-disable */



class StudentsTab extends Component {
    render() {
        return (
            <li className="header__navbar--tab students">
                <Link to={ `/${ pathes.students }` }
                      className="link"
                      activeClassName="is-active" >
                      Студентам
                </Link>
            </li>
        )
    }
};

export default StudentsTab;



