import React                    from 'react';
import { syncHistoryWithStore } from 'react-router-redux';
import {
    Router,
    Route,
    browserHistory,
    IndexRoute
}                                from 'react-router';
import store                     from './store';
import { pathes }                from './constants';
import {
    HomePage,
    AboutPage,
    StudentsPage,
    CurriculumPage,
    PartnersPage,
    ContactsPage,
    FullstackPage,
    WithHeaderAndFooter
}                                from './components';


import App from './App';


/* eslint-disable */



const history = syncHistoryWithStore(browserHistory, store);

const routes = (
    <Router history = { history }>
        <Route path = { pathes.init } component = { App }>
            <Route component={ WithHeaderAndFooter } >
                <IndexRoute component = { HomePage } />
                <Route path = { pathes.home } component = { HomePage } />
                <Route path = { pathes.about } component = { AboutPage } />
                <Route path = { pathes.students } component = { StudentsPage } />
                <Route path = { pathes.curriculum } component = { CurriculumPage } />
                <Route path = { pathes.partners } component={ PartnersPage } />
                <Route path = { pathes.contacts } component={ ContactsPage } />
                <Route path = { pathes.fullstack } component={ FullstackPage } />
            </Route>

            <Route path = { pathes.other } component={ WithHeaderAndFooter } />
        </Route>
    </Router>
);

export default routes;


