import React, { Component }   from 'react';
import './ContactsInfo.styl';

/* eslint-disable */





class ContactsInfo extends Component {
    render() {
        const { contactsInfo }  = this.props,
              { phones, email } = contactsInfo;
        return (
                <section className="contacts-info">
                    <h4 className="contacts-info__title">Наши контакты</h4>
                    <div className="contacts-info__phones">
                        {
                            phones && phones.map((phone, i) => (
                                <div className="contacts-info__phone" key={ i }>
                                    <h4 className="contacts-info__phone-title">{ phone.title }</h4>
                                    <p className="contacts-info__phone-number">{ phone.number }</p>
                                    {
                                        phone.workingTime && phone.workingTime.map((time, i) => (
                                            <p className="contacts-info__working-time" key={ i }>{ time }</p>
                                        ))
                                    }
                                </div>
                            ))
                        }
                    </div>
                </section>
        )
    };
};


export default ContactsInfo;





