import React, { Component }    from 'react';
import FontAwesome             from 'react-fontawesome';
import './CoursesInfo.styl';




/* eslint-disable */

class CoursesInfo extends Component {
    render() {

        const { coursesInfo }  = this.props,
              { title, list }  = coursesInfo;


        const icons = ["file-code-o", "code-fork", "thumbs-o-up", "clock-o", "calendar-check-o", "desktop", "check-square-o", "code", "cogs", "graduation-cap", "address-book-o", "line-chart"];


        return (
                <section className="courses-info-wrapper">
                    <div className="courses-info">
                        <div className="courses-info__body">
                            <h4 className="courses-info__body-title">{ title }</h4>
                            <div className="courses-info__body-description-wrapper">
                                {
                                    list && list.map((el, i) => (
                                        <div className="courses-info__body-description" key={ i }>
                                            <FontAwesome name={ icons[i] } className="courses-info__body-description-icon"/>
                                            {
                                                el && el.map((el, index) => (<p className="courses-info__body-description-block" key={ index }>{ el }</p>))
                                            }
                                        </div>

                                    ))
                                }
                            </div>
                        </div>
                        <img className="courses-info__svg" src="./images/home/courses-info.svg" alt=""/>
                    </div>
                </section>
        )
    }
};



export default CoursesInfo;



