import {types} from "./../constants";



const initialState = {
    isLoading:   false,
    isError:     false,
    error:       null,
    isModalOpen: false,
    data:        {}
};

const mainInfoReducer = function (state = initialState, action) {
    const {type, payload} = action;

    switch (type) {
        case types.MAIN_INFO_GET_PENDING:
            return {
                ...state,
                isLoading: true,
                isError: false
            };
        case types.MAIN_INFO_GET_FULFILLED:
            return {
                ...state,
                data: payload,
                isLoading: false,
                isError: false
            };
        case types.MAIN_INFO_GET_REJECTED:
            return {
                ...state,
                isLoading: false,
                isError: true,
                error: payload
            };
        default:
            return state;
    }
};

export default mainInfoReducer;


