import React, { Component }    from 'react';
import { Button }              from './../../../_UI';
import './NewCoursesDescription.styl';




/* eslint-disable */

class NewCoursesDescription extends Component {
    render() {
        return (
                <div className="new-courses__description">
                    <h4 className="new-courses__description-title">Курсы PHP / Frontend с трудоустройством</h4>
                    <p className="new-courses__description-body">Получите 4 месяца курсов PHP и технического английского + стажировку сроком на 3 месяца.</p>
                    <p className="new-courses__description-body">Зарплата стажера - от $250 до $350 в месяц. Контракт на стажировку подписывается при старте курсов.</p>
                    <a className="new-courses__description-link" href="https://ain.ua/2017/02/03/php-academy-rezultaty-kursov-s-trudoustrojstvom-i-plany-na-2017" target="_blank">Читать интервью с выпускниками на AIN.ua → </a>
                    <Button text="Отправить заявку" className="new-courses__description-btn"/>
                </div>

        )
    }
};
export default NewCoursesDescription;



