import React from 'react';
import './Loader.styl';





/* eslint-disable */

const Loader = (props) => {
    const { className } = props,
          childrenArray = new Array(8).join("a").split("a");
    return (
        <div className="loader-wrapper">
            <div className={`loader ${className}`}>
                {
                    childrenArray.map((child, i) => <div key={ i } className="side"></div> )
                }
            </div>
        </div>
    )
};

export default Loader;




