import React, { Component }   from 'react';
import './LocationMapIframe.styl';

/* eslint-disable */




class LocationMapIframe extends Component {

    render() {
        const { iframeSrc } = this.props;
        return (
            <iframe className   = "location-map__iframe"
                    src         = { iframeSrc }
                    width       = "600"
                    height      = "450"
                    frameBorder = "0"
                    allowFullScreen />
        )
    };
};


export default LocationMapIframe;





