import React, { Component }           from 'react';
import { Link }                       from 'react-router';
import { pathes }                     from './../../../constants';
import './MainTab.styl';


/* eslint-disable */

class MainTab extends Component {
    constructor(){
        super();
    };
    render() {
        const { isMainPage }  = this.props;
        return (
            <li className="header-nav__tab main-tab">
                <Link to={ `/${ pathes.main }` }
                      className={`link ${isMainPage ? "is-active" : ""}`}
                      activeClassName="is-active">
                      Главная
                </Link>
            </li>
        )
    }
}


export default MainTab;


