import React, { Component } from 'react';
import { Link }             from 'react-router';
import './TabConstructor.styl';

/* eslint-disable */



class TabConstructor extends Component {
    render() {
        const { text, className, path } = this.props;
        return (
            <li className = { `header__navbar--tab ${ className }` }>
                <Link to              = { path }
                      className       = "link"
                      activeClassName = "is-active" >
                      { text }
                </Link>
            </li>
        )
    }
};

export default TabConstructor;



