import React, { Component }    from 'react';
import {
    NewCoursesDescription,
    NewCoursesOffices
}                              from './';
import './NewCourses.styl';




/* eslint-disable */

class NewCourses extends Component {
    render() {
        const { newCourses } = this.props;
        return (
                <section className="new-courses">
                    <NewCoursesOffices info={ newCourses } />
                    <NewCoursesDescription/>
                </section>

        )
    }
};






export default NewCourses;



