import React, { Component } from 'react';
import { Link }             from 'react-router';
import { pathes }           from './../../../constants';
import './VideoTutorialsTab.styl';

/* eslint-disable */



class VideoTutorialsTab extends Component {
    render() {
        return (
            <li className="header__navbar--tab video-tutorials">
                <Link to={ `/${ pathes.about }` }
                      className="link"
                      activeClassName="is-active" >
                      Видеоуроки
                </Link>
            </li>
        )
    }
};

export default VideoTutorialsTab;



