import React, { Component }    from 'react';
import './StudentsDiscountDescription.styl';




/* eslint-disable */

class StudentsDiscountDescription extends Component {
    render() {
        return (
            <div className="students-discount__description">
                <h4 className="students-discount__description-title">Скидка для студентов!</h4>
                <div className="students-discount__description-body">При подписании договора предъяви студенческий: получи -33% на любой курс.</div>
                <div className="students-discount__description-limit">предложение действует до 01.06.2017</div>
            </div>
        )
    }
};






export default StudentsDiscountDescription;



